.gpt3__footer
{
        color: white;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: var(--color-footer);

}
.gpt3__footer-heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}
.gpt3__footer-heading h1 
{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}
.gpt3__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 1rem;
    border: 1px solid #fff;
    margin-bottom: 10rem;

}

.gpt3__footer-btn p 
{
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    color:#fff;
    word-spacing: 2px;
}
.gpt3__footer-links
{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;

    width: 100%;
    text-align: left;
}
.gpt3__footer-links_div 
{
    width: 250px;
    margin: 1rem;

}
.gpt3__footer-links-logo{
    display: flex;
    flex-direction: column;
}
.gpt3__foooter-links-logo img{
    width:118px;
    height: 30px;

    margin-bottom: 1rem;
}

.gpt3__foooter-links-logo p
{ 
    font-family: var(--font-family);
    font-size: 12px;
    color:#fff;
    line-height: 14px;
}
.gpt3__footer-links_div
{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.gpt3__footer-links_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    color:#fff;
    line-height: 16px;
    margin-bottom: 3rem;
}
.gpt3__footer-links_div p{
    /* font-family: var(--font-family);
    font-size: 14px;
    color:#fff;
    line-height: 16px; */
    margin-bottom: 1rem;
}

.gpt3__footer-copyright
{
    font-family: Gilroy-Medium;
    font-size: 12px;
    line-height: 14px;
    font-feature-settings: 'liga' off;
    color:#fff;
    margin-top: 1rem;
}
.gpt3__footer-copyright p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color:#fff;
}